@import '../base';

.c-science-atoms {
  padding: 1.8rem 2.5rem 2.5rem;

  &__text {
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;

    &-pretitle {
      padding-bottom: 1.125rem;
    }

    &-title {
      padding-bottom: 1rem;
    }

    &-subtitle {
      color: $s-color--grey-green;
    }
  }

  &__atom {
    padding: 2.375rem 0 4.625rem;
    max-width: 21.25rem;
    margin: auto;
    width: 100%;
  }
  &__cta-btn {
    margin-top: 1.5625rem;
    color: $s-color--font-1;
    font-weight: $s-font--weight-bold;
  }

  @include t-media-query($s-small-up) {
    display: flex;
    align-items: center;
    justify-content: space-between;

    &__text {
      max-width: 28.5625rem;

      &.is-first {
        text-align: left;
        align-items: flex-start;
        margin-right: auto;
        padding-right: 2rem;
        flex: 1 1 0;
      }

      &.is-last {
        text-align: right;
        align-items: flex-end;
        margin-left: auto;
        padding-left: 2rem;
        flex: 1 1 0;
      }

      &-pretitle {
        padding-bottom: 1.1875rem;
      }

      &-title {
        padding-bottom: 1.4375rem;
      }

      &-subtitle {
        padding: 0;
      }
    }

    &__atom {
      flex: 1 1 0;
      padding: 0;
    }
  }

  @include t-media-query($s-medium-up) {
    padding: 1.8rem 3.8125rem 2.5rem;
  }

  @include t-media-query($s-xlarge-up) {
    padding: 1.8rem 8.8125rem 2.5rem;
  }

  @include t-media-query($s-xxlarge-up) {
    max-width: 2000px;
    margin: 0 auto;
    padding: 1.8rem 12.8125rem 2.5rem;
  }
}